<template>
    <div ref="echartLine" class="echart-line" id="echart-line"></div>
</template>
<script>
// ECharts 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/line";
// 
import "echarts/lib/component/tooltip";
import "echarts/lib/component/grid";
export default {
  props: {
    numbers: {
      type: Array,
      default: () => []
    },
    color: {
      type: String,
      default: "#3ed6bf"
    },
    width: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    maxNum() {
      return Math.max(...this.numbers);
    },
    option: function() {
      return {
        tooltip: {
          trigger: "item",
          formatter: function(data) {
            return data.value + "";
          },
          position: "top"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: ["1", "2", "3", "4", "5"],
          // 
          axisLine: {
            lineStyle: {
              color: "#eee"
            }
          },
          // 
          splitLine: {
            show: true,
            // 
            lineStyle: {
              // 
              color: ["#eee"]
            }
          },
          // x
          axisTick: {
            show: false
          },
          // x
          axisLabel: {
            textStyle: {
              color: "#999"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            min: 0,
            max: this.maxNum,
            interval: 50,
            // Y
            show: true,
            splitLine: {
              // 
              show: false
            },
            axisLabel: {
              formatter: "{value}",
              textStyle: {
                color: "#c7c7c7"
              }
            },
            // y
            axisLine: { show: false },
            // y
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            type: "line",
            lineWidth: 15,
            data: this.numbers,
            itemStyle: {
              normal: {
                color: this.color,
                lineStyle: {
                  width: 2
                }
              }
            }
          }
        ]
      };
    }
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echartLine);
    this.myChart.setOption(this.option);
  },
  watch: {
    numbers: function() {
      this.myChart.setOption(this.option);
    },
    // 
    width: function() {
      const width = document.getElementById("echart-line").clientWidth;
      this.myChart.resize({ width });
    }
  }
};
</script>

<style scoped>
.echart-line {
  height: 100%;
  width: 100%;
  display: inline-block;
}
</style>



